main,
article {
  & header {
    margin-bottom: 1.25rem;
  }
}

.body {
  background-color: $theme-color-white;
  display: flex;
  gap: 1.25rem;
  flex-wrap: wrap;

  @include breakpoint-down(md) {
    padding: $theme-spacer-below;
  }

  @include breakpoint-up(md) {
    padding: $theme-spacer-above;
  }
}

%nested-link {
  position: relative;

  .link {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 10;
  }
}

.post {
  flex: 1 0
    percentage(
      ($theme-content-columns - $theme-content-sidebar-columns - 1) /
        $theme-content-columns
    );
  order: 1;
  overflow-x: auto;
  @extend %nested-link;

  &:not(:last-of-type) {
    padding-bottom: 1.25rem;
    margin-bottom: 1.25rem;
    border-bottom: 1px solid $theme-accent-dark-grey;
  }
}

.list {
  flex: 1 0
    percentage(
      ($theme-content-columns - $theme-content-sidebar-columns - 1) /
        $theme-content-columns
    );
  order: 1;
  overflow-x: auto;
}

%nested-a a {
  color: $theme-color-primary;

  &:hover {
    color: $theme-color-secondary;
    text-decoration: underline;
  }
}

.content,
.warning {
  & p {
    @extend %nested-a;
  }

  & ul,
  & ol {
    list-style: revert;
    margin: 0 0 20px 40px;
  }

  & ul ul,
  & ol ol,
  & ol ul,
  & ul ol {
    margin: 0 0 0 40px;
  }

  & li {
    margin-bottom: 5px;
    @extend %nested-a;
  }

  & p + ul,
  & p + ol {
    margin-top: 20px;
  }

  @include breakpoint-down(md) {
    & ul,
    & ol {
      margin: 0 0 20px 20px;
    }

    & ul ul,
    & ol ol,
    & ol ul,
    & ul ol {
      margin: 0 0 0 20px;
    }
  }
}

.content {
  :not(:only-child, :last-child) {
    margin-bottom: $theme-spacer-bottom;
  }
}

.warning {
  padding: 20px 10px;
  text-align: center;
  border: 1px solid #ddd;
}

.board {
  display: flex;
  width: 100%;
  gap: 0.5rem;
  flex-wrap: wrap;

  header {
    flex: 0 0 100%;
  }

  .card {
    flex: 1 0 30%;
    min-width: 250px;
    padding: $theme-spacer-below;
    overflow-y: clip;
    @extend %nested-link;

    @include breakpoint-down(md) {
      flex-basis: 100%;
    }

    header {
      margin: 0;
    }

    .square {
      width: 80px; // #{"{{ .Site.Params.Theme.Related.XL | default "80px" }}"};
      height: 80px;
      margin: 0 .5rem .5rem 0;
    }
  }
}
