@import "../mixins/breakpoint";

.share,
.social {
  justify-content: space-evenly;
  display: flex;
  flex-wrap: wrap;

  a {
    font-size: 1rem;
  }

  .icon {
    display: inline-block;
    vertical-align: top;
  }

  @each $brand, $color in $theme-brands {
    .icon.#{$brand} {
      &:hover,
      &:active {
        color: $color;
      }
    }
  }
}
